export function clickSize(el) {
    const cardBlock = document.querySelectorAll(".card");
    const cardInfo = document.querySelectorAll(".card__description");
    if (el.textContent === "4 elements") {
        cardBlock.forEach((elem, i) => {
            elem.style.width = "24%";
            elem.style.minHeight = "225px";
            cardInfo[i].style.display = "block";
        });
    }
    else if (el.textContent === "6 elements") {
        cardBlock.forEach((elem, i) => {
            elem.style.width = "15.66%";
            elem.style.minHeight = "100px";
            cardInfo[i].style.display = "none";
        });
    }
}
export function controlSliders(filter, array, arrayEl, num, value) {
    const writerResFromSlider = filter.querySelectorAll(".fromSlider");
    const writerResToSlider = filter.querySelectorAll(".toSlider");
    const textFrom = filter.querySelectorAll(".filter-block__min-value");
    const textTo = filter.querySelectorAll(".filter-block__max-value");
    const nonValue = filter.querySelectorAll(".filter-block__non-value");
    array.sort((a, b) => a - b);
    if (arrayEl.length !== 0) {
        writerResFromSlider[num].value = String(array[0]);
        writerResToSlider[num].value = String(array[array.length - 1]);
        nonValue[num].textContent = " <-> ";
        textFrom[num].textContent = `${value} ${array[0]}`;
        textTo[num].textContent = `${value} ${array[array.length - 1]}`;
    }
    else {
        nonValue[num].textContent = "Нічога не знойдзена";
        textFrom[num].textContent = "";
        textTo[num].textContent = "";
    }
}
let prodIdCount = {};
export function AddToCart(cardButtonAdd) {
    cardButtonAdd.addEventListener("click", () => {
        if (!localStorage.getItem("product") ||
            !Object.keys(JSON.parse(localStorage.getItem("product") || "{}")).includes(`${Number(cardButtonAdd.getAttribute("data-id"))}`)) {
            cardButtonAdd.textContent = "Drop From Cart";
            prodIdCount = JSON.parse(localStorage.getItem("product") || "{}");
            prodIdCount[Number(cardButtonAdd.getAttribute("data-id"))] = 1;
            localStorage.setItem("product", JSON.stringify(prodIdCount));
        }
        else if (Object.keys(JSON.parse(localStorage.getItem("product") || "{}")).includes(`${Number(cardButtonAdd.getAttribute("data-id"))}`) &&
            Object.keys(prodIdCount).includes(`${Number(cardButtonAdd.getAttribute("data-id"))}`)) {
            delete prodIdCount[Number(cardButtonAdd.getAttribute("data-id"))];
            localStorage.setItem("product", JSON.stringify(prodIdCount));
            cardButtonAdd.textContent = "Add To Cart";
        }
        else if (Object.keys(JSON.parse(localStorage.getItem("product") || "{}")).includes(`${Number(cardButtonAdd.getAttribute("data-id"))}`) &&
            !Object.keys(prodIdCount).includes(`${Number(cardButtonAdd.getAttribute("data-id"))}`)) {
            prodIdCount = JSON.parse(localStorage.getItem("product") || "{}");
            delete prodIdCount[Number(cardButtonAdd.getAttribute("data-id"))];
            localStorage.setItem("product", JSON.stringify(prodIdCount));
            cardButtonAdd.textContent = "Add To Cart";
        }
    });
}
